.footer {
  padding: 10px 0;
}

.footer__content {
  display: flex;
  justify-content: space-between;
  max-width: 1300px;
  margin: 0 auto;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: rgba(113, 116, 126, 0.5);

  @media (max-width: $tablet-width) {
    flex-direction: column-reverse;
    align-items: center;
    font-size: 14px;
  }
}

.footer__copy {
  padding: 10px 18px;
}

.footer__menu {
  display: flex;
  padding: 10px 18px;
}

.footer__menu-item:not(:last-child) {
  margin-right: 22px;
}

.footer__menu-link {
  display: inline;
  position: relative;
  overflow: hidden;

  &:after {
    content: "";
    position: absolute;
    z-index: -1;
    right: 0;
    width: 0;
    bottom: -5px;
    background: rgba($highlight-color, 0.5);
    height: 1px;
    transition-property: width;
    transition-duration: 0.3s;
    transition-timing-function: ease-out;
  }

  &:hover:after,
  &:focus:after,
  &:active:after {
    left: 0;
    right: auto;
    width: 100%;
  }
}
