.payment-form {
  padding: 34px 64px;
  box-shadow: 1px 3px 11px rgba(0, 0, 0, 0.15);
  border-radius: 5px;

  @media (max-width: $laptop-width) {
    padding: 34px 24px;
  }

  @media (max-width: $mobile-width) {
    padding: 24px 14px;
  }
}

.payment-form__group-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  .payment-form__group {
    width: 50%;

    @media (max-width: $tablet-width) {
      width: 100%;
    }

    &--small {
      width: calc(25% - 10px);
      @media (max-width: $tablet-width) {
        width: calc(50% - 8px);
      }
    }
  }
}

.payment-form__group {
  margin-bottom: 20px;

  @media (max-width: $mobile-width) {
    margin-bottom: 10px;
  }
}

.payment-form__label {
  display: flex;
  flex-direction: column;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;

  @media (max-width: $mobile-width) {
    font-size: 14px;
    line-height: 20px;
  }
}

.payment-form__input {
  width: 100%;
  padding: 18px;
  margin-top: 10px;
  border: 1px solid $secondary-text;
  border-radius: 5px;
  font-size: 18px;
  line-height: 22px;
  color: $secondary-text;

  @media (max-width: $mobile-width) {
    padding: 10px;
    margin-top: 5px;
  }

  &:focus {
    border-color: $highlight-color;
  }
}

.payment-form__btn {
  width: 100%;
  margin-top: 10px;
  margin-bottom: 25px;
  padding: 20px;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  color: $light-text;
  background-color: $highlight-color;
  border-radius: 5px;

  @media (max-width: $mobile-width) {
    padding: 10px;
    margin-bottom: 0;
  }

  &:hover {
    background-color: darken($highlight-color, 10%);
  }
}
