.payment {
  padding: 90px 0;

  @media (max-width: $tablet-width) {
    padding: 40px 0;
  }
}

.payment__method {
  padding: 25px 0;
}

.payment__title {
  position: relative;
  display: inline-block;
  margin-bottom: 30px;
  font-weight: 900;
  font-size: 24px;
  line-height: 28px;

  &:after {
    position: absolute;
    content: "";
    border-bottom: 2px solid $dark-text;
    left: 0;
    bottom: -6px;
    width: 100%;
  }
}

.payment__subtitle {
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  color: rgba(113, 116, 126, 0.76);
}

.payment__form {
  display: none;
}

.payment__form--show {
  display: block;
}

.payment__form-info {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 318px;
  font-weight: 900;
  font-size: 24px;
  line-height: 28px;

  @media (max-width: $tablet-width) {
    min-height: 429px;
  }

  @media (max-width: $mobile-width) {
    min-height: 288px;
  }
}
