.header {
  border-bottom: 1px solid rgba(196, 196, 196, 0.3);
}

.header__logo {
  display: inline-flex;
  align-items: center;
}

.header__logo-text {
  margin-left: 20px;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
  color: $dark-text;

  @media (max-width: $mobile-width) {
    font-size: 14px;
    margin-left: 10px;
  }

  span {
    font-weight: bold;
    text-transform: uppercase;
  }

color: #000000;
}

.header__content {
  display: flex;
  justify-content: space-between;
  padding: 18px 20px;
  max-width: 1300px;
  margin: 0 auto;

  @media (max-width: 425px) {
    padding: 18px 10px;
  }
}
