.header__user-wrap {
  position: relative;

  &:after {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 8px;
    height: 8px;
    border-left: 3px solid rgba(0, 0, 0, 0.46);
    border-bottom: 3px solid rgba(0, 0, 0, 0.46);
    transform: rotate(-45deg);
    box-sizing: border-box;
    z-index: 10;
    transition: transform 0.1s ease;
  }

  &:hover:after {
      transform: translate(0, 3px) rotate(135deg);
  }
}

.header__user-menu {
  position: absolute;
  transform: translateY(-100%);
  padding: 5px;
  padding-bottom: 10px;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  width: 100%;
  height: 50px;
  background: #ffffff;
  border-right: 1px solid rgba(196,196,196,.3);
  border-bottom: 1px solid rgba(196,196,196,.3);
  z-index: 1;
  transition: transform 0.3s ease;
}

.header__user-wrap:hover {
  .header__user-menu {
    transform: translateY(0);
  }
}

.header-user {
  padding-right: 20px;
  position: relative;
  display: flex;
  align-items: center;
  background: #ffffff;
  cursor: pointer;
  z-index: 2;
}

.header-user:hover {
  .header-user__name {
    opacity: 0.7;
  }
}

.header-user__img {
  position: relative;
  width: 46px;
  height: 50px;
  border-radius: 10px;
  overflow: hidden;
}

.header-user__icon {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

.header-user__name {
  position: relative;
  margin-left: 16px;
  padding-right: 10px;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  color: $dark-text;

  @media (max-width: $mobile-width) {
    display: none;
  }
}

